<template lang="">
    <PreloaderVac/>
    <TitleVac :title="title"/>
    <div class="blog">
        <div class="container">
            <div class="blog-main">
                <div class="blog-main_for" v-for="(blogCart, index) in dataPagination" :key="index">
                    <!-- <router-link class="blog-main_cart" to="" > -->
                        <div class="interesting-slide_content" @click="cartClick(blogCart.id)">
                            <img :src="blogCart.image">
                            <div class="interesting-slide_main">
                                <p>10 May, 2020</p>
                                <h5>{{blogCart.titleCart}}</h5>
                            </div>
                        </div>
                    <!-- </router-link> -->
                </div>
                
            </div>
            <div class="btn-toolbar">
                <div class="btn-group">
                    <button v-for="p in totalPaginas()" @click = "getDataPaginas(p)" class="buttonP" 
                    :class="isActive(p)">
                        
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import store from '@/store'
import TitleVac from '../components/TitleVac.vue'
import PreloaderVac from './PreloaderVac.vue'

export default {

    data(){
        return{
            title: 'Our blog',
            perPage: null,
            dataPagination: [],
            paginaActual: 1,
            width: null
        }
    },
    computed: {
        blogCarts(){
            return this.$store.getters.blogCarts
        },
    },

    methods:{
        innerWidth(){
            this.width = window.innerWidth;
            if(this.width > 769){
                this.perPage = 9
            }
            if(this.width <= 768 && this.width >= 577){
                this.perPage = 8
            }
            if(this.width <= 576){
                this.perPage = 6
            }
        },
        totalPaginas(){
            return Math.ceil(this.blogCarts.length / this.perPage) 
        },
        getDataPaginas(noPagina){
            this.dataPagination = []
            this.paginaActual = noPagina
            let ini = (noPagina * this.perPage) - this.perPage
            let fin = (noPagina * this.perPage)
            this.dataPagination = this.blogCarts.slice(ini, fin) 
            window.scrollTo(0,0)           
        },
        cartClick(id){
            this.$router.push({name: 'Article', query: {'blog': id}})
        },
        isActive(noPagina){
            return noPagina == this.paginaActual ? 'activeP' : ''
        }
    },

    created() {
        window.addEventListener('resize', this.innerWidth);
        this.innerWidth();
    },

    mounted() {
        this.getDataPaginas(1);
    },

    components: {
      TitleVac,
      PreloaderVac,
    },
    
    watch:{
        perPage(){
            if(this.width > 769){
                this.perPage = 9
            }
            if(this.width <= 768 && this.width >= 577){
                this.perPage = 8
            }
            if(this.width <= 576){
                this.perPage = 6
            }
            this.getDataPaginas(1)
        }
    },
}
</script>
<style lang="scss">

@import '../assets/scss/breakpoints.scss';


                                                    //blog

.blog {
    margin: 40px 0;
    @include breakpoints(sm){
        margin: 30px 0;
    }
}
.blog-main {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.blog-main_for{
    width: 32%;
    cursor: pointer;
    @include breakpoints(md){
        width: 48%;
    }
    @include breakpoints(sm){
        width: 100%;
    }
}
.blog-main_cart {
}
.interesting-slide_content {
    img{
        width: 100%;
    }
}
.interesting-slide_main {
}

                                    //pagination

                                    
.btn-toolbar {
    margin-top: 20px;
    padding-bottom: 20px;
    @include breakpoints(sm){
        margin-top: 10px;
        padding-bottom: 0px;
    }
}
.btn-group {
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;

    .activeP{
        opacity: 1;
        transition: .3s opacity ease;
    }
}  

.buttonP{
    width: 10px;
    height: 10px;
    background: #606276;
    opacity: 0.3;
    border-radius: 50%;
    cursor: pointer;
}
    
</style>